import Layout from "@components/layout";
import Seo from "@components/seo";
import React from "react";
import BlogsData from "@screens/blogs";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import BreadCrumb from "@components/common/breadcrumb";

const BlogsScreen = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Blogs", link: "" },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <BlogsData
        blogImages={props?.data?.allImageSharp?.edges}
        db={db}
        siteUrl={props?.data?.site?.siteMetadata?.siteUrl}
      />
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const logoObj = data?.allImageSharp?.edges?.find((image) => {
    return image?.node?.gatsbyImageData?.images?.fallback?.src?.includes(
      "new_logo.png"
    );
  });
  return (
    <Seo
      title="Justtry Technologies Blog: Updates, news, Articles"
      metaTitle="Justtry Technologies Blog: Updates, news, Articles"
      description="Dive into the world of innovation with Justtry Technologies' blog. Uncover the latest tech trends, gain insights from industry experts, and empower yourself with cutting-edge knowledge. Explore a fusion of expertise and creativity as we unravel the future of technology together. Justtry Technologies - where curiosity meets innovation."
      image={logoObj?.node?.gatsbyImageData?.images?.fallback?.src}
    ></Seo>
  );
};

export default BlogsScreen;

export const query = graphql`
  query MyQuery {
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
    allDataJson {
      edges {
        node {
          id
          keyId
          slug
          author_name
          blog_created_date
          title
          short_description
          description
          meta_title
          meta_description
          image
          category
          tags
        }
      }
      pageInfo {
        perPage
        hasNextPage
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
        siteUrl
      }
    }
  }
`;
